.search-filter{
display: flex;
justify-content: center;
align-items: center;
margin-left: 150px;
margin-top: 50px;
}
.search-filter input{
    background-color: var(--light-gray);
    width: 720px;
    height: 55px;
    border-radius: 5px;
    border: none;
    outline: none;
    color: gray;
    padding: 20px;
    transition: background-color 0.5s ease;
}
.dark .search-filter input{
    background-color: var(--dark-slate);

}
